import hFormAlert from '@/components/h-form-alert/h-form-alert.vue'
export default {
	components: {
		hFormAlert
	},
	data() {
		return {
			modalValue:"",
			modal6: false,
		}
	},
	methods: {
		confirm(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了确定"
			})
		},
		cancel(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了取消"
			})
		},
		hide6() {
			this.modal6 = false
		},
		refuse(e){
			this.modalValue="您确定拒绝该学生吗？"
			this.modal6 = true
		},
		approve(e){
			this.modalValue="您确定通过该学生吗？"
			this.modal6 = true
		},
		jobdetail(e){
			uni.navigateTo({
				url:"../job-detail/job-detail"
			})
		}
	}
}
